<template>
  <div class="mh-100vh">
    <deng-long2></deng-long2>
    <div class="d-flex fz-16 border-box">
      <!-- 左边 -->
      <div class="flex-shrink mh-100vh bg-pink" style="width: 120px;">
        <div class="d-flex flex-center left text-white transition-all " v-for="(item,index) in list" :key="index" :class="changeIndex == index ? selectClassName : defaultClassName " style="height: 40px" @click="handleChangeIndex(index)">
          {{item.className}}
        </div>
      </div>
      <!-- 右边 -->
      <div class="flex-1 m-100vh overflow-auto" style="padding-bottom: 100px">
        <template v-if="list[changeIndex].subList">
          <div class="mt-10 animate__animated animate__fast animate__bounceInRight" v-for="(item,index) in list[changeIndex].subList" :key="index" >
            <div class="right-title fz-18 fw-6 text-center py-10">
              {{item.title}}
            </div>
            <div class="d-flex a-center flex-wrap mx-05">
              <template v-for="(v,i) in item.rightArr">
                <div 
                :key="i" 
                class="w-50 border-box px-5 mb-10"
                @click="jumpDetail(v)"
                >
                  <img class="d-block br-4" :src="v.img" style="width: 100%;height: 150px;">
                  <span class="fz-14 d-block w-100 text-center pt-05 mt-10">{{v.title}}</span>
                </div>
              </template>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
let list = []
import PageJump from '../components/PageJump.vue'
import { lists } from '@/utils/categoryArr.js'
export default {
  components: { PageJump },
  data() {
    return {
      changeIndex: 0,
      selectClassName: 'bg-red fz-18',
      defaultClassName: 'bg-pink',
      list: lists
    }
  },
  mounted() {},
  methods: {
    handleChangeIndex(index) {
      this.changeIndex = index
    },
    jumpDetail(v) {
      this.$router.push({
        path: '/banana',
        query: v
      })
    }
  },
}
</script>

<style scoped>
.left {
  cursor: pointer;
}
</style>
